
.mask {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
}
.playlist {
  position: absolute;
  bottom: 0;
  width: calc(100% - 2rem);
  height: 50%;
  background-color: #fbfbfb;
  padding: 0.5rem 1rem;
}
.playlist-head {
  height: 2rem;
  line-height: 2rem;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
}
.playlist-head .title {
    -ms-flex: 5;
        flex: 5;
    text-align: center;
    color: black;
}
.playlist-head .clearList {
    float: right;
    -ms-flex: 1;
        flex: 1;
    color: #bbb;
    text-align: center;
}
.playlist-art {
  height: 90%;
  overflow: auto;
}
.playlist-art .list-li {
    border-bottom: 1px solid #ccc;
    height: 3rem;
    line-height: 3rem;
}
.playlist-art .list-li .songname {
      color: #343434;
      max-width: 20rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: inline-block;
      vertical-align: top;
}
.playlist-art .list-li .songart {
      font-size: 0.8rem;
      color: #868686;
      margin-left: 0.2rem;
      max-width: 6rem;
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
}
.playlist-art .list-li .del-list {
      float: right;
      cursor: pointer;
      color: #868686;
      display: inline-block;
      width: 3rem;
      text-align: center;
}
.playlist-art .list-li.active .songname {
    color: #c62f2f;
}
.playlist-art .list-li.active .songart {
    color: #c62f2f;
}



