
.search-head {
  position: fixed;
  top: 0;
  left: 5rem;
  height: 4rem;
  z-index: 101;
}
.search-head .search-inp-wrap {
    border-bottom: 1px solid white;
    display: inline-block;
    height: 59%;
    margin-top: 1rem;
}
.search-head .searchtext {
    width: 18rem;
    height: 100%;
    line-height: 100%;
    font-size: 1.2rem;
    outline: none;
    background: none;
    border: none;
    color: white;
    text-indent: 0.5em;
}
.search-items {
  border-bottom: 1px solid white;
  height: 3rem;
  line-height: 3rem;
  padding-left: 3rem;
}
.search-items .songname {
    display: inline-block;
    max-width: 21rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: top;
}
.search-items .hot-res {
    float: right;
    margin-right: 4rem;
    font-size: 0.8rem;
}
.search-list-warp {
  position: fixed;
  bottom: 0;
  z-index: 100;
  overflow: auto;
  height: calc(100% - 4rem);
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  color: white;
}



